// fv
.fv {
  background-image: url('  https://github.com/susumu3/sho_sample_middleEx/blob/main/image/spa-header.png?raw=true');
  background-position: center;
  background-size: cover;
  min-height: 580px;
  position: relative;
  @include mq('sp') {
    min-height: 85vh;
  }
  .fv__ttl {
    position: absolute;
    top: 57%;
    left: 50%;
    transform: translate(-50%, -50%);
    h1 {
      font-weight: 800;
      font-size: rem(34);
      color: $textColorWhite;
      line-height: 2.253;
      letter-spacing: 0.08rem;
      @include mq("sp") {
        font-size: rem(28);
      }
    }
  }
}



// spa
.spa {
  .sectionWrapper {
    padding-top: 36px;
    padding-bottom: 105px;
    @include mq('lpc') {
      padding-top: 26px;
      padding-bottom: 100px;    
    }
    @include mq('tab') {
      padding-top: 26px;
      padding-bottom: 80px;    
    }
    @include mq('tab') {
      padding-top: 20px;
      padding-bottom: 60px;    
    }
    .breadcrumbs{
      ol {
        padding: 0;
        margin: 0;
        list-style: none;
        li {
          display: inline;
          &:after {
            content: '>';
            padding: 0 .2em;
          }
          &:last-child::after {
            content: '';
          }
          a {
            list-style: none;
            letter-spacing: 0.07em;
            &:hover {
              text-decoration: underline;
            }
            span {
              font-size: rem(14);
            }
          }
        }
      }
    }
    .spa__intro {
      margin-top: 89px;
      text-align: center;
      @include mq('tab') {
        margin-top: 69px;
      }
      @include mq('sp') {
        margin-top: 35px;
      }
      p {
        font-size: rem(16);
        @include mq('sp') {
          font-size: rem(14);
        }
        span {
          line-height: 2;
          @include mq('sp') {
            line-height: 1.5;
            letter-spacing: -0.03em;
          }
        }
      }
      @include mq('sp') {
        br {
          display: none;
        }
        span {
          line-height: 1.6;
          letter-spacing: 0.01em;
          display: inline-block;
          &:nth-of-type(even) {
            padding-top: 15px;
          }
        }
      }
    }
    .spaIntroductionWrapper {
      width: 100%;
      .spaIntroductionWrapper__introduction {
        width: 100%;
        min-height: 438px;
        position: relative;
        @include mq('lpc') {
          min-height: auto;
          position: static;
        }
        &:first-of-type {
          margin-top: 73px;
          @include mq('tab') {
            margin-top: 63px;
          }
          @include mq('sp') {
            margin-top: 53px;
          }
        }
        &:not(:first-of-type) {
          margin-top: 84px;
          @include mq('tab') {
            margin-top: 54px;
          }
          @include mq('sp') {
            margin-top: 44px;
          }
        }
        .introductionImageWrapper {
          position: absolute;
          width: 525px;
          max-width: 525px;
          height: 300px;
          img {
            height:100%;
            object-fit: cover;
          }
          @include mq('lpc') {
            position: static;
            width: auto;
          }
          @include mq('sp') {
            width: 100%;
            margin-right: auto;
            margin-left: auto;
          }
        }
        .introduction__description {
          position: absolute;
          background-image: url('https://github.com/susumu3/sho_sample_middleEx/blob/main/image/room-introduction-bg.png?raw=true');
          background-repeat: no-repeat;
          background-size: cover;
          width: 813px;
          max-width: 813px;
          height: 381px;
          @include mq('lpc') {
            margin-top: -30px;
            position: static;
            width: auto;
            max-width: 700px;
            height: auto;
          }
          @include mq('sp') {
            margin-top: 0;
            width: 100%;
            margin-right: auto;
            margin-left: auto;
          }
          h2 {
            font-size: rem(24);
            span {
              display: inline-block;
            }
            @include mq('sp') {
              font-size: rem(20);
              line-height: 1.5;
              text-align: center;
            }
          }
          p{
            margin-top: 43px;
            font-size: rem(16);
            line-height: 2;
            @include mq('lpc') {
              br {
                display: none;
              }
            }
            @include mq('sp') {
              margin-top: 30px;
              font-size: rem(14);
              line-height: 1.5;
            }
          }
          .footnote {
            margin-top: 39px;
            @include mq('sp') {
              margin-top: 25px;
            }
            .footnoteText {
              margin-top: 3px;
              font-size: rem(14);
              line-height: 1.5;
              text-indent: -1em;
              padding-left: 1em;
              @include mq('sp') {
                line-height: 1.3;
                font-size: rem(12);
              }   
            }
          }        
        }
        .img1 {
          top: 0;
          left: 0;
          z-index: 2;
        }
        .int1 {
          padding: 60px 68px 110px 235px;
          bottom: 0;
          right: 0;
          @include mq('lpc') {
            padding: 50px 80px 80px 60px;
            margin-left: auto;
          }
          @include mq('sp') {
            padding: 30px 30px 60px 30px;
          }
        }
        
        .img2 {
          top: 0;
          right: 0;
          z-index: 2;
          @include mq('lpc') {
            margin-left: auto;
          }
        }
        .int2 {
          padding: 60px 270px 80px 60px;
          bottom: 0;
          left: 0;
          @include mq('lpc') {
            padding: 50px 80px 80px 60px;
          }
          @include mq('sp') {
            padding: 30px 30px 60px 30px;
          }
        }
      }
      .efficacySpa {
        border-top: 1px solid #E7E7E7;
        border-bottom: 1px solid #E7E7E7;
        margin-top: 156px;
        @include mq('sp') {
          margin-top: 35px;
        }
        .efficacySpaWrapper {
          padding-top: 95px;
          padding-bottom: 74px;
          max-width: 880px;
          margin-right: auto;
          margin-left: auto;
          @include mq('sp') {
            padding-top: 45px;
            padding-bottom: 45px;
          }
          .efficacySpa-ttl {
            h3 {
              text-align: center;
              font-size: rem(26);
            }
          }
          .efficacySpaInner {
            margin-top: 47px;
            dl {
              display: flex;
              flex-wrap: wrap;
              width: 100%;
              dt {
                font-size: rem(15);
                border-top: 1px solid #E7E7E7;
                padding-top: 9.5px;
                padding-bottom: 9.5px;
                width: 25%;
                line-height: calc(32 / 16);
                @include mq('sp') {
                  width: 30%;
                }
                span {
                  display: inline-block;
                }
                &:last-of-type {
                  border-bottom: 1px solid #E7E7E7;
                }
              }
              dd {
                font-size: rem(15);
                border-top: 1px solid #E7E7E7;
                padding-top: 9.5px;
                padding-bottom: 9.5px;
                width: 75%;
                line-height: 1.6;
                @include mq('sp') {
                  width: 70%;
                }
                &:last-of-type {
                  border-bottom: 1px solid #E7E7E7;
                }
              }
            }
          }
        }
      }
    }
  }
}


